<template>
	<section>
		<div class="inr-banner">
			<img :src="this.imgUrlBanner" alt=""/>
		</div>
		<div class="inner">
			<div class="inf-info inf-sxtj">当前位置：<a href="">首页 &gt;</a><span class="inf-info-tet"> 国内出厂价</span></div>
			<div class="inf-r-c clearfix inr-que newAdd">
				<span v-for="(item,index) in oilDataList">
	        		<a href="javascript:;" @click="getdomesticOilPrice(item.itemCode,index)" :class=" index == num ? 'oil_active' : ''">{{item.itemName}}</a>
				</span>
	        </div>
	        <div class="searchFn">
	        	<span class="demonstration">价格日期</span>
				<el-date-picker 
				   value-format="yyyy-MM-dd"
			  	  format="yyyy-MM-dd"
			      v-model="dataRange"
			      type="daterange"
			      range-separator="至"
			      start-placeholder="开始日期"
			      size="small"
			      class="w200"
			      end-placeholder="结束日期">
			    </el-date-picker>
			    <span class="demonstration dateSearch" @click="dataSearch()"><a href="javascript:;">查询</a></span>
	        </div>
      <div class="inr-que">
       <table>
        <tr class="inrq-t">
          <th>产品名称</th>
          <th>价格</th>
          <th>价格单位</th>
          <th>价格日期</th>
          <th>区域名称</th>
          <th>价格来源</th>
          <th>价格趋势</th>
          <th>价格变动</th>
        </tr>
        <tr class="inf-tab"  v-for="(item,index) in dataList">            
          <td>{{item.productName}}</td>
          <td>{{item.lowPrice}}~{{item.highPrice}}</td>
          <td>{{item.priceUnit}}</td>
          <td>{{item.priceDate}}</td>
          <td>{{item.areaName}}</td>
          <td>{{item.priceSource}}</td>
          <td v-if="item.priceTrend == 1">上涨</td>
          <td v-if="item.priceTrend == 0">持平</td>
          <td v-if="item.priceTrend == -1">下降</td>
          <td>{{item.lowPriceChange}}~{{item.highPriceChange}}</td>
        </tr>
       </table> 
       <div class="c-page" style="text-align: center; margin-top: 30px;" v-if="dataList.length>0">
            <el-pagination @current-change="pageFn" layout="prev, pager, next" :total="total" :page-size="pageSize"> </el-pagination>
      </div>   
      </div>
                                                                                            
    </div>
     <el-dialog
		title="尊敬的用户:"
		:visible.sync="dialogVisible"
		width="30%"
		:before-close="handleClose">
		<span>您查看的数据是付费内容，请订阅浏览</span>
		<span slot="footer" class="dialog-footer">
			<el-button @click="dialogVisible = false">不,继续免费试试</el-button>
			<el-button type="info" disabled>￥<span>498</span>/年 </el-button>
		</span>
	</el-dialog>
	</section>
</template>

<script>


	import {domesticOilData,oilData,queryCarousels} from '../../api/info';
	export default {
		data() {
			return {
				 dialogVisible:true,
				imgUrlBanner:'',
                total:0,
                pageNum:1,
                pageSize:20,
				dataList:[],
                hidenProductCode:40,
                oilDataList:[],
                num :0,
                dataRange:''
			}
		},
		methods: {
			
            pageFn(sty){    // 分页
				var objS={
					 "pageNum": sty,
					 "pageSize":this.pageSize,
                     "productCode" : this.hidenProductCode,
                      'startTime' : this.dataRange[0],
          			 'endTime' : this.dataRange[1]
				}
				domesticOilData(objS).then((res)=>{
					this.dataList=res.datas;
				})
            },
            
			getData:function(){
//              var productCode = window.localStorage.getItem('productCode');
//              this.hidenProductCode = productCode;
				var parameterData ={
					 "productCode": this.hidenProductCode,
					 "pageNum": this.pageNum,
					 "pageSize":this.pageSize 
                }
//              console.log(parameterData);
				domesticOilData(parameterData).then((res)=>{
                    this.dataList=res.datas;  //内容区域
                    this.total = res.total;
				});
           },
           getOilData: function() {
				oilData({setCode: "DOMESTIC_PRODUCT"}).then((res)=>{
				this.oilDataList=res.datas;
				})
			},
          	getdomesticOilPrice(para,index){
          		this.hidenProductCode = para
          		this.num = index
          		
          		if(this.dataRange){
          			var parameterData ={
						 "productCode": para,
						 "pageNum": this.pageNum,
						 "pageSize":this.pageSize,
						'startTime' : this.dataRange[0],
	          			'endTime' : this.dataRange[1],
	                }
          		}else{
          			var parameterData ={
						 "productCode": para,
						 "pageNum": this.pageNum,
						 "pageSize":this.pageSize,
						'startTime' : '',
	          			'endTime' : '',
	                }
          		}
//              console.log(parameterData);
				domesticOilData(parameterData).then((res)=>{
                    this.dataList=res.datas;  //内容区域
                    this.total = res.total;
				});
          	},
          	dataSearch(){
//        		console.log(this.dataRange)
				if(this.dataRange){
					var parameterData ={
	          			'startTime' : this.dataRange[0],
	          			'endTime' : this.dataRange[1],
						"productCode": this.hidenProductCode,
						"pageNum": this.pageNum,
						"pageSize":this.pageSize 
	               }
				}else{
					var parameterData ={
	          			'startTime' : '',
	          			'endTime' : '',
						"productCode": this.hidenProductCode,
						"pageNum": this.pageNum,
						"pageSize":this.pageSize 
	               }
				}
          		
				domesticOilData(parameterData).then((res)=>{
//					console.log(res)
                    this.dataList=res.datas;  //内容区域
                    this.total = res.total;
				});
          	},
          	
          	getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			}
		},
		mounted(){    
			this.getData();
			this.getOilData();
			this.getBanner()
    }
	}

</script>
<style>
.w350{width:350px!important;}
.childTable{width:100%;}
.childTable tr:last-child{border-bottom: 0px;}
.newAdd{ width: 96% !important;}
.newAdd span{display: inline-block; padding: 10px;}
.oil_active{color: #317EE7 !important;}
.searchFn{background: #FFFFFF; padding: 20px 25px;}
.dateSearch{background: #317EE7; display: inline-block;padding:7px; width: 60px; text-align: center; border-radius: 5px; margin-left: 50px;}
.dateSearch a{color: #FFFFFF;}

</style>


